import React, { Component, Fragment } from 'react';
import Types from 'prop-types';
import { FormControlType } from './FormControlType';
import Radio from '../radio';
import Select from '../select';
import DateInput from '../dateInput/DateInput';
import PhoneNumberInput from '../phoneNumberInput/PhoneNumberInput';
import Checkbox from '../checkbox/Checkbox';

export default class FormControl extends Component {
  static DISABLED_LABEL_TYPES = [FormControlType.CHECKBOX];
  static UNIQUE_ID = 0;

  static propTypes = {
    type: Types.oneOf([
      FormControlType.RADIO,
      FormControlType.CHECKBOX,
      FormControlType.SELECT,
      FormControlType.FILE,
      FormControlType.DATE,
      FormControlType.DATETIME,
      FormControlType.TEL,
      FormControlType.NUMBER,
      FormControlType.HIDDEN,
      FormControlType.PASSWORD,
      FormControlType.TEXT,
      FormControlType.TEXTAREA,
      FormControlType.UPLOAD,
    ]),
    name: Types.string.isRequired,
    id: Types.string,
    label: Types.string,
    placeholder: Types.string,
    helpText: Types.string,
    step: Types.number,
    locale: Types.string,
    // uploadAccept: Types.bool,
    // uploadIcon: Types.string,
    // uploadTooLargeMessage: Types.string,
    // helpImage: Types.string,
    options: Types.arrayOf(
      Types.shape({
        label: Types.string.isRequired,
        value: Types.oneOfType([Types.number, Types.string, Types.bool, Types.object]).isRequired,
        secondary: Types.string,
      }),
    ),
    disabled: Types.bool,
    readOnly: Types.bool,
    required: Types.bool,
    onChange: Types.func.isRequired,
    onBlur: Types.func,
    onFocus: Types.func,
    minLength: Types.number,
    maxLength: Types.number,
    min: Types.number,
    max: Types.number,
    // pattern: Types.instanceOf(RegExp),
    defaultValue: Types.oneOfType([Types.string, Types.number, Types.bool, Types.object]),
    // uploadOptions: Types.shape({}),
    // textFormat: Types.string,
    // validationAsync: Types.func
    searchPlaceholder: Types.string,
  };

  static defaultProps = {
    type: FormControlType.TEXT,
    id: FormControl.getUniqueId(),
    label: null,
    placeholder: null,
    helpText: null,
    locale: null,
    options: [],
    step: 1,
    // uploadAccept: false,
    // uploadIcon: null,
    // uploadTooLargeMessage: null,
    // helpImage: null,
    disabled: false,
    readOnly: false,
    required: true,
    onBlur() {},
    onFocus() {},
    min: null,
    max: null,
    minLength: null,
    maxLength: null,
    defaultValue: undefined,
    searchPlaceholder: null,
  };

  constructor(props) {
    super(props);

    this.handleOnFocus = this.handleOnFocus.bind(this);
    this.handleOnChange = this.handleOnChange.bind(this);
    this.handleOnBlur = this.handleOnBlur.bind(this);
  }

  state = {
    selectedOption: undefined,
    value: this.props.defaultValue || undefined,
    touched: false,
  };

  static getUniqueId() {
    FormControl.UNIQUE_ID += 1;
    return `${FormControl.UNIQUE_ID}`;
  }

  /**
   * autocomplete hides our form help so we need to disable it when help text
   * is present. Chrome ignores autocomplete=off, the only way to disable it is
   * to provide an 'invalid' value, for which 'disabled' serves.
   */
  getAutocompleteStatus() {
    return this.props.helpText ? 'disabled' : 'on';
  }

  getRadioClassNames(option) {
    let classes = '';
    if (this.props.disabled) {
      classes = `${classes} disabled`;
    }
    if (option.secondary) {
      classes = `${classes} radio-lg`;
    }
    return classes;
  }

  handleOnChange(event) {
    const { onChange } = this.props;
    if (!event || !event.target) {
      if (event.label && event.value) {
        this.setState({ value: event.value });
        return onChange(event.value);
      }
      this.setState({ value: event });
      return onChange(event);
    }

    this.setState({ value: event.target.value });
    return onChange(event.target.value);
  }

  handleOnFocus(event) {
    return this.props.onFocus(event);
  }

  handleOnBlur(event) {
    if (!this.state.touched) {
      this.setState({ touched: true });
    }
    return this.props.onBlur(event);
  }

  showLabel() {
    return !FormControl.DISABLED_LABEL_TYPES.includes(this.props.type);
  }

  renderField() {
    const {
      name,
      id,
      label,
      placeholder,
      step,
      locale,
      type,
      options,
      disabled,
      readOnly,
      required,
      minLength,
      maxLength,
      min,
      max,
      defaultValue,
      searchPlaceholder,
    } = this.props;
    const { value } = this.state;
    switch (type) {
      case FormControlType.RADIO: {
        return (
          <Fragment>
            {options.map((option, index) => (
              // eslint-disable-next-line react/no-array-index-key
              <Fragment key={index}>
                <Radio
                  label={option.label}
                  id={`${index}`}
                  name={name}
                  onFocus={this.handleOnFocus}
                  onChange={() => {
                    this.setState({ selectedOption: option });
                    this.handleOnChange(option);
                  }}
                  onBlur={this.handleOnBlur}
                  checked={
                    this.state.selectedOption && this.state.selectedOption.value === option.value
                  }
                  disabled={disabled}
                />
                {option.secondary && <small>{option.secondary}</small>}
              </Fragment>
            ))}
          </Fragment>
        );
      }
      case FormControlType.CHECKBOX: {
        return (
          <Checkbox
            checked={value}
            required={required}
            disabled={disabled}
            label={label}
            onFocus={this.handleOnFocus}
            onChange={this.handleOnChange}
            onBlur={this.handleOnBlur}
          />
        );
      }
      case FormControlType.SELECT: {
        return (
          <Select
            selected={
              this.state.selectedOption &&
              options.find(option => this.state.selectedOption.value === option.value)
            }
            options={options}
            onChange={newValue => {
              this.setState({ selectedOption: newValue });
              this.handleOnChange(newValue);
            }}
            onFocus={this.handleOnFocus}
            onBlur={this.handleOnBlur}
            required={required}
            disabled={disabled}
            searchPlaceholder={searchPlaceholder}
          />
        );
      }
      case FormControlType.NUMBER: {
        return (
          <input
            type="number"
            className="form-control"
            id={id}
            step={step}
            placeholder={placeholder}
            value={value}
            readOnly={readOnly}
            required={required}
            disabled={disabled}
            min={min}
            max={max}
            onChange={this.handleOnChange}
            onBlur={this.handleOnBlur}
            onFocus={this.handleOnFocus}
            autoComplete={this.getAutocompleteStatus()}
          />
        );
      }
      case FormControlType.HIDDEN: {
        return <input type="hidden" name={name} value={defaultValue} />;
      }
      case FormControlType.PASSWORD: {
        return (
          <input
            type="password"
            className="form-control"
            id={id}
            placeholder={placeholder}
            value={value}
            readOnly={readOnly}
            required={required}
            disabled={disabled}
            onChange={this.handleOnChange}
            onBlur={this.handleOnBlur}
            onFocus={this.handleOnFocus}
            autoComplete={this.getAutocompleteStatus()}
          />
        );
      }
      case FormControlType.DATE:
      case FormControlType.DATETIME: {
        return (
          <DateInput
            disabled={disabled}
            size="md"
            value={value}
            locale={locale}
            onChange={this.handleOnChange}
          />
        );
      }
      case FormControlType.TEL: {
        return (
          <PhoneNumberInput
            locale={locale}
            value={value}
            disabled={disabled}
            required={required}
            size="md"
            searchPlaceholder={searchPlaceholder}
            placeholder={placeholder}
            onChange={this.handleOnChange}
          />
        );
      }
      case FormControlType.TEXTAREA: {
        return (
          <textarea
            className="form-control"
            id={id}
            name={name}
            placeholder={placeholder}
            value={value}
            readOnly={readOnly}
            required={required}
            // pattern={this.props.pattern}
            minLength={minLength}
            maxLength={maxLength}
            onChange={this.handleOnChange}
            onFocus={this.handleOnFocus}
            onBlur={this.handleOnBlur}
            disabled={disabled}
            autoComplete={this.getAutocompleteStatus()}
          />
        );
      }
      case FormControlType.TEXT:
      default: {
        return (
          <input
            type="text"
            className="form-control"
            id={id}
            name={name}
            placeholder={placeholder}
            value={value}
            readOnly={readOnly}
            required={required}
            // pattern={this.props.pattern}
            minLength={minLength}
            maxLength={maxLength}
            onChange={this.handleOnChange}
            onFocus={this.handleOnFocus}
            onBlur={this.handleOnBlur}
            disabled={disabled}
            // tw-text-format={this.props.textFormat}
            autoComplete={this.getAutocompleteStatus()}
          />
        );
      }
    }
  }

  render() {
    const { label, id } = this.props;
    return label && this.showLabel() ? (
      <Fragment>
        <label htmlFor={id}>{label}</label>
        {this.renderField()}
      </Fragment>
    ) : (
      this.renderField()
    );
  }
}



